.margin {
  margin: 0px;
}
.padding {
  padding-left: 0px;
  padding-right: 0px;
}
.paper {
  position: fixed;
  z-index: 10;
  width: 10vw;
}

.lottie {
  z-index: -100;
  width: 100%;
  margin: 0px;
  padding: 0px;
  position: absolute;
}

#myCanvas {
  /* position:fixed; */
  height: 500px;
  width: 500px;
  z-index: 10;
  background-color: transparent !important;
}
.visualheight {
  position: fixed;
  height: 100vh;
  z-index: 9999999;
}
.yellowbutton {
  background: url(button.svg);
  height: 100px;
  width: 100px;
  z-index: 9999999;
  text-align: center;
  border: transparent;
  bottom: 0px;
}

.image1 {
  width: 100%;
  z-index: -100;
  margin: 0;
  padding: 0;
}

.textura {
  width: 100%;
  z-index: 999;
  position: absolute;
}

canvas[resize] {
  width: 100%;
  height: 100%;
}
